.login {
    width: 413px;
    height: 552px;
    margin: auto;

    background: #ffffff;
    border-radius: 2px;
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1), -1px -1px 2px rgba(0, 0, 0, 0.1);
    margin-top: 50px;
}

.login h2 {
    padding-top: 35px;

    font-family: 'Montserrat', sans-serif;
    font-size: 26px;
    font-weight: 600;
    font-style: normal;
    line-height: 32px;
    color: #000000;
    text-align: center;
}

.login form {
    max-width: 347px;
    margin-top: 31px;
    padding-left: 33px;

    text-align: left;
}

.login_button {
    cursor: pointer;

    width: 100%;
    height: 36px;
    margin-top: 70px;
    padding: 0;

    font: inherit;
    font-size: 16px;
    font-weight: 500;
    line-height: 20px;
    color: #ffffff;
    text-align: center;
    letter-spacing: 0.01em;

    background: none;
    background: #366eff;
    border: none;
    border-radius: 30px;
    outline: inherit;
}
.login_button:disabled {
    opacity: 0.5;
}

.login_error {
    color: red;
}

.login_haveAccount {
    margin-top: 50px;

    font-size: 14px;
    font-weight: 600;
    line-height: 24px;
    color: #000000;
    text-align: center;

    opacity: 0.5;
}

.login_loginReference {
    margin-top: 0px;

    font-size: 16px;
    font-weight: 600;
    line-height: 24px;
    color: #366eff;
    text-align: center;
    text-decoration-line: underline;
}