.registration_input {
    position: relative;
}

.registration_input:not(:last-child) {
    margin-bottom: 18px;
}

.registrationLabel {
    display: block;

    margin-bottom: 0;

    font-size: 13px;
    font-weight: 400;
    line-height: 20px;
    color: #000000;

    opacity: 0.5;
}

.registration_input input {
    cursor: pointer;

    display: block;

    width: 100%;

    font-size: 16px;
    color: #000000;

    border: none;
    border-bottom: 1px solid rgb(0, 0, 0, 0.2);
}

.registration_input input:focus {
    outline: none;
}

.registration_eye {
    /*position: absolute;*/
    /*display: inline-block;*/
    /*right: 0;*/
    /*bottom: 6px;*/
    /*cursor: pointer;*/

    cursor: pointer;

    position: absolute;
    right: 8px;
    bottom: 7px;

    display: inline-block;

    width: 20px;
}
